.skill-icon img{
    width: 5vw;
    height: auto;
}
.skills-card{
    border-radius: 0.65rem;
    border: 1.5px solid rgb(117, 220, 175);
    padding: 2.5rem;
    text-align: center;
    background: black;
    backdrop-filter: blur(1rem);
    position: relative;
    transition: all 0.3s ease;

}

.skills-card:hover,
.skills-card.active{
    background:linear-gradient(180deg,rgb(165, 232, 220),rgb(56, 208, 155));
}

