@media(max-width){
    .skills-card{
        padding: 2rem;
    }
    .skills-card span{
        font-size: 1rem;
    }
    .skill-icon{
        width: 3rem;
        height: auto;

    }
    .skill-icon img{
        width: 2rem;
    };
}
    
