.skill-container{
   
    margin: 4rem 0;
    position: relative;
}



.skills-container h5{
    
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3.5rem;
    /* justify-content: center; */
    text-align: center;
}
.skills-content{
    display: flex;
    align-items: flex-start;
    gap:3rem;
}
.skills{
    flex: 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2,1fr);
}
.skills-info{
    flex: 1;
}


@media(max-width:1025px){
    .skills{
        padding-left: 1rem;
    }
    .skills,
    .skills-content{
        grid-gap: 2rem;
    }
}
@media(max-width:768px){
    .skills-content{
        flex-direction: column-reverse;
        gap:3rem;
    }
    .skills,
    .skills-info{
        width: 100%;
    }
    .skill-container h5{
        font-size: 1.3rem;
        margin-bottom: 2rem;
    
    }
    .skill-container::after,
    .skill-container::before{
        width:18rem;
        height: 18rem;
    }
}
@media(max-width:600px){
    .skill-container{
        padding: 0;
    }
}


 
