.skills-info-card{
    min-height: 23rem;
    border-radius: 0.65rem;
    border: 1.5px solid  rgb(117, 220, 175);
    background-color: black;
    backdrop-filter: blur(2rem);
}
.skills-info-card h6 {
    margin-top: 0;
    font-size: 1.2rem;
    font-weight: 500;
    padding: 0.8rem 2rem;
    
    background: linear-gradient(180deg, rgb(117, 220, 175),rgb(117, 220, 175));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    border-bottom: 1.5px solid rgb(117, 220, 175);
  }

  .skills-info-content{
    padding: 2rem;
  }
  .skill-info{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .skill-info p{
    /* margin-top: -1.5rem; */
    font-size: 1rem;
    font-weight: 500;
  }

  .skill-info .percentage{
color:  rgb(117, 220, 175);;
  }

  .skill-progress-bg{
    width: 100%;
    height: 0.5rem;
    background:#555454 ;
    border-radius: 0.5rem;
    margin:1rem 0;
    overflow: hidden;
  }
  .skill-progress{
    width: 0%;
    height: 0.5rem;
    background: linear-gradient(90deg,rgb(117, 220, 175),rgb(117, 220, 175));
    border-radius: 0.5rem;
    transition: all 0.5s ease-in-out;
  }


  @media(max-width:1025px){
    .skills-info-card h6{
        font-size: 1.1rem;

    }
    .skill-info p{
        font-size: 0.9rem;
    }
    .skills-info-content{
        padding: 1.5rem;
    }
  }
  