
.hero-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: relative;
}

.hero-content {
    flex: 1;
    margin-left: 2rem;
    justify-content: center;
    align-items: center;
}

.hero-content h2 {
    color: white;
    font-size: 2.5rem;
    font-weight: 600;
    line-height: 5rem;
    margin-bottom: 1rem;
}

.title {
    display: flex;
    align-items: center;
    
}

.title h3 {
    margin-left: 1rem;
    font-size: 1.5rem;
   
}


.hero-content p {
    width: 80%;
    font-size: 1rem;
    font-weight: 400;
    line-height: 2rem;
   
}

.hero-img {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin-top: 5rem;
}

.hero-img > div {
    display: flex;
    align-items: flex-end;
    gap: 2rem;
}

.hero-img > div > img {
    width: 25rem;
    transition: all .3s ease;
}

.tech-icon img {
    width: 30vw;
    height: 30vw;
    border-radius: 10px;
}
.introduce{
 
   
    margin-top: 2rem;
    font-size: 0.8rem;
    line-height: 2rem;
}



@media (max-width: 768px) {
    .hero-container {
        flex-direction: column;
        justify-content: center;
    }

    .hero-img {
        order: -1; /* 將圖片移到hero-content上方 */
        margin-bottom: 2rem;
    }

    .hero-content {
        margin-left: 0;
        margin-bottom: 2rem;
        text-align: center;
    }

    .hero-content h2 {
        font-size: 2rem;
        line-height: 3rem;
    }

    .hero-content p {
        width: 100%;
    }

    .tech-icon img {
        width: 50vw;
        height: 50vw;
    }

    .title {
        width: 100%;
        display: flex;
        justify-content: center;
        
    }

    .title h3 {
        margin-left: 1rem;
        font-size: 1.5rem;
       
    }

    .title h2 {
        font-size: 1.5rem;
        text-align: center;
       
    }
}

