.project-card {
    background-color: #1a1a2e;
    border-radius: 0.5rem;
    overflow: hidden;
    transition: all 0.3s ease;
    position: relative;
  }
  
  .project-image {
    width: 100%;
    height: 400px;
    background-size: cover;
    background-position: center;
    overflow: hidden;
  }
  
  .project-content {
    padding: 1rem;
    text-align: center;
   
  }
  
  .project-content h5 {
    color: rgb(117, 220, 175); /* 主體顏色 */
    margin-bottom: 0.5rem;
  }
  
  .project-content p {
    color: #a8a8a8;
  }
  
  .project-link {
    display: none;
    margin-top: 1rem;
    text-decoration: none;
    color: rgb(117, 220, 175); /* 主體顏色 */
    font-weight: bold;
  }
  
  .project-card:hover {
    transform: translateY(-10px);
    border: 2px solid rgb(117, 220, 175); /* 主體顏色 */
  }
  
  .project-card:hover .project-link {
    display: block;
  }