.experience-container{
    margin:4rem 0;
    position: relative;
}
.experience-container h5{
  text-align: center;
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3rem;
}
.arrow-left,
.arrow-right {
  width: 2.2rem;
  height: 2.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color:  rgb(117, 220, 175);
  border-radius: 0.65rem;
  border: 1.5px solid  rgb(117, 220, 175);
  background: #130f2a;
  position: absolute;
  top: 55%;
  z-index: 2;
  cursor: pointer;
}

.arrow-left span,
.arrow-right span {
  font-size: 2rem;
  font-weight: 500;
}
.arrow-left{
    left:-2rem
}
.arrow-right{
    right:-2rem
}
