.mobile-menu {
    width: 55vw;
    height: 100vh; /* 将高度设置为视口高度 */
    display: none; /* 初始状态下隐藏菜单 */
    background-color: black;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 999;
    opacity: 0; /* 初始状态下不透明度为0 */
    box-shadow: 0px 29px 88px rgba(0, 0, 0, 0.3);
    transition: all 0.3s ease;
    transform: translateX(-100%); /* 初始状态下将菜单移出视口 */
}


.mobileLogo{
    margin-top: -4vw;
    margin-left: 0;
    margin-bottom: 10vw;

}

.mobile-menu.active {
    opacity: 1; /* 激活状态下设置不透明度为1 */
    transform: translateX(0); /* 激活状态下将菜单移入视口 */
}

.mobile-menu-container {
    width: 60vw;
    height: 100vh;
    background-color: black;
    padding: 2rem;
}

.mobile-menu img {
    margin-bottom: 3rem;
}

.mobile-menu ul {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    list-style: none;
    padding-left: 0;
    margin-left: 0; /* 调整列表的左边距 */
}

@media (max-width: 769px) {
    .mobile-menu {
        display: block; /* 仅在屏幕宽度小于769px时显示菜单 */
    }
}
