.projects-title {
    text-align: center;
    margin-bottom: 2rem;
    color: #ffffff;
  }
  
  .project-container {
    width: 90%;
    margin: 0 auto;
  }
  
  .toggle-group {
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
  }
  
  .toggle-btn {
    background-color: #1a1a2e;
    color: rgb(117, 220, 175); /* 主體顏色 */
    border: 1.5px solid rgb(117, 220, 175); /* 主體顏色 */
    padding: 0.5rem 1rem;
    margin: 0 0.5rem;
    cursor: pointer;
    border-radius: 0.5rem;
    transition: all 0.3s ease;
  }
  
  .toggle-btn:hover {
    background-color: rgb(117, 220, 175); /* 主體顏色 */
    color: #1a1a2e;
  }
  
  .project-box {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
  }
  
  @media (max-width: 768px) {
    .project-box {
      grid-template-columns: 1fr;
    }
  }
  