body {
    color: #ffff;
}

.nav-wrap {
    background-color: rgba(0, 0, 0, 1);
    padding: 0.5rem 0;
    position: sticky;
    top: 0;
    z-index: 30;
    backdrop-filter: blur(50px);

}

.nav-content {
    max-width: 1300px;
    display: flex;
    height: 25px;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0;
    margin: 0 auto;
}

.logo {
    padding-left: 50px;
    font-size: 1.5rem;
}

.nav-content ul {
    display: flex;

    align-items: center;
    gap: 0.5rem;
    list-style: none;
    padding-right: 50px;

}

.nav-content li {
    margin: 0 1.5rem
}

.menu-item {
    text-decoration: none;
    font-size: 0.9rem;
    font-weight: 500;
    color: #ffff;
    position: relative;
    cursor: pointer;
}

.menu-item::before {
    content: "";
    width: 2rem;
    height: 0.2rem;
    background: linear-gradient(90deg, #ffff 0%, #ffff 100%);
    border-radius: 0.5rem;
    position: absolute;
    bottom: -0.6rem;
    opacity: 0;
    transform: translateX(-1.5rem);
    transition: all 0.3s ease;
}

.menu-item:hover::before {
    width: 100%;
    transform: translateX(0);
    opacity: 1;
}

.menu-btn {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 0.4rem;
    color: black;
    background: linear-gradient(90deg, #ffff 0%, #ffff 100%);
    line-height: 0;
    cursor: pointer;
    transition: all 0.4s ease;
    display: none;
}



/* rwd後的nav */

@media (max-width:769px){
.menu-btn{
    display: block;
}

.nav-content ul{
    display: none;
}

.logo{
    font-size: 1rem;
}

}
@media (max-width:1325px) {
    
    .nav-wrap{
        padding: 0.2rem;
    }

}
